import React from "react"
import { Form, Input, Button, message } from "antd"
import { compareStrings } from "../../../utils"

function fetchKantoPokemon(limit) {
  return fetch(`https://pokeapi.co/api/v2/pokemon?limit=${limit}`)
    .then((r) => r.json())
    .then((r) => {
      const { results: pokemons } = r
      const pokemon = pokemons[Math.floor(Math.random() * pokemons.length)]
      return fetch(pokemon.url)
        .then((r) => r.json())
        .then((p) => {
          if (p.name === "mr-mime") p.name = "mr. mime"
          if (p.name === "nidoran-f") p.name = "nidoran"
          if (p.name === "nidoran-m") p.name = "nidoran"
          return p
        })
    })
}

const PokemonTrivia = ({ onDone }) => {
  const [pokemon, setPokemon] = React.useState()
  const [done, setDone] = React.useState(false)
  const [form] = Form.useForm()

  React.useEffect(() => {
    fetchKantoPokemon(151).then(setPokemon)
  }, [])

  const onFinish = (values) => {
    const diff = compareStrings(pokemon.name, values.answer)
    if (diff > 0.95) {
      setDone(true)
      setTimeout(onDone, 3000)
    } else {
      if (diff > 0.9) {
        message.error("Your Answer is Close!")
      } else {
        message.error("Incorrect answer, try again!")
        form.setFieldsValue({ answer: undefined })
      }
    }
  }

  if (!pokemon) return null

  return (
    <div style={{ maxWidth: 500, margin: "auto", padding: 20, textAlign: "center" }}>
      <img
        alt={pokemon.order}
        src={`https://pokeres.bastionbot.org/images/pokemon/${pokemon?.id}.png`}
        width="100%"
        style={{ filter: done ? "" : "brightness(0)", transition: "all 1500ms" }}
      />
      <div style={{ padding: 20 }}>
        {done && <h1>{pokemon.name}</h1>}
        {!done && (
          <>
            <h1>Who's that pokemon?</h1>
            <Form form={form} onFinish={onFinish}>
              <Form.Item
                name="answer"
                rules={[
                  {
                    required: true,
                    message: "Please input your answer!",
                  },
                ]}
              >
                <Input />
              </Form.Item>
              <Form.Item>
                <Button type="primary" htmlType="submit">
                  Take a guess!
                </Button>
              </Form.Item>
            </Form>
          </>
        )}
      </div>
    </div>
  )
}

export default PokemonTrivia
